#root {
    max-width: 100vw;
}


main {
    background-color: rgb(241, 241, 241);
    display: flex;
    flex-direction: column;
    font-family: 'Roboto', sans-serif;   
    position: relative;
    width: 100%;

    .titulo-section {
        font-size: clamp(1.3rem, 3vw, 1.5rem);
        font-weight: 700;
        position: relative;
        
        &::after {
            background-color: #b42424;
            bottom: -.4rem;
            content: '';
            height: 3px;
            left: 2px;
            position: absolute;
            width: 46px;
        }
    }

    header {
        backdrop-filter: blur(2px);
        background-color: rgba(241, 241, 241, .9);
        min-height: 85px;
        position: fixed;
        width: 100vw;
        z-index: 999;
    
        .container {
            align-items: center;
            display: flex;
            justify-content: space-between;
            margin: 0 auto;
            padding: 1rem 1.5rem;
            max-width: 1400px;
            
    
            .logo {
                animation: entryAnimationR 1.5s ease-in-out forwards;
    
                img {
                    width: 60px;
                }
            }
    
            .btn-menu-hamburguer {
                animation: entryAnimationL 1.5s ease-in-out backwards;
                border: none;
                background: none;
    
                svg {
                    height: 50px;
                    width: 50px;
    
                    &.menu {
                        stroke: #000;
                    }
    
                    &.x {
                        stroke: #f1f1f1;
                        opacity: 1;
                        position: relative;
                        z-index: 2;
                    }
                }
            }
    
            nav {
                background-color: rgba(0, 0, 0, .95);
                display: flex;
                justify-content: center;
                left: -100vw;
                overflow: hidden;
                position: absolute;
                top: 0;
                transition: .5s;
                z-index: 1;
                width: 100%;
    
                &.open {
                    left: 0;
                }
    
                ul {
                    align-items: center;
                    display: flex;
                    flex-direction: column;
                    height: 100vh;
                    justify-content: center;
                    position: relative;
                    text-align: center;
                    top: 0;
                    width: 100%;
                    z-index: 1;
                    
                    li {
                        cursor: default;
                        padding: 1rem;
                        width: 100%;
    
                        &:active {
                            background-color: rgba(250, 250, 250, 0.1);
                        }
    
                        a {
                            color: #f1f1f1;
                            font-size: 1.2rem;
                            font-weight: lighter;
                        }
                    }
                }
    
                .redes-sociais {
                    background-color: #f1f1f1;
                    border-radius: 100% 100% 0 0;
                    display: flex;
                    bottom: 0;
                    gap: 2.5rem;
                    justify-content: center;
                    padding-bottom: 3rem;
                    padding-top: 2rem;
                    position: absolute;
                    width: 110vw;
                    z-index: 1;
                    
                    a {
                        i {
                            border-radius: 2rem;
                            font-size: 2.1rem;
                            transition: .2s;
        
                            &.fa-whatsapp {
                                color: rgb(34, 146, 34);
                                
                                &:active {
                                    box-shadow: 0 0 100px 5px green;
                                }
                            }
                            &.fa-facebook {
                                color: rgb(22, 87, 173);
                                
                                &:active {
                                    box-shadow: 0 0 100px 5px blue;
                                }
                            }
                            &.fa-instagram {
                                color: rgb(146, 38, 128);
                                
                                &:active {
                                    box-shadow: 0 0 100px 5px purple;
                                }
                            }
                        }
                    }
                }
            }
        }


        @keyframes entryAnimationR {
            from {
                margin-right: 2rem;
                opacity: 0;
            } to {
                margin-right: 0;
                opacity: 1;
            }
        }
        @keyframes entryAnimationL {
            from {
                margin-left: 2rem;
                opacity: 0;
            } to {
                margin-left: 0;
                opacity: 1;
            }
        }
    }
    

    .home {
        background-image: linear-gradient(#f1f1f1 95%, #e5e5e5 5%);
        height: 100%;
        max-height: 100vh;
        width: 100%;
    
        .container-conteudo {
            display: flex;
            flex-direction: column;
            gap: 2rem;
            justify-content: center;
            height: 95%;
            margin-top: 1rem;
            padding-block: 110px 10px;
            padding-inline: 1.5rem;
            width: 100%;
    
            .conteudo {
                animation: entryAnimation 2s ease-in-out forwards;
                display: flex;
                flex-direction: column;
                width: 100%;
        
                .text {
                    display: flex;
                    flex-direction: column;
                    gap: .5rem;
            
                    h1 {
                        font-size: clamp(2rem, 3vw, 2.7rem);
                        font-weight: bold;
                        width: 200px;
                    }
            
                    p {
                        color: #777;
                        font-size: clamp(1.1rem, 1.5vw, 1.3rem);
                        font-weight: bold;
                    }
        
                    .btn-home {
                        display: none;
                    }
                }
            
                picture {
                    align-self: center;
                    position: relative;
            
                    &::before {
                        bottom: 20%;
                        border-radius: 5rem 1rem 1rem 0;
                        box-shadow: 0 0 30px 15px #111;
                        content: '';
                        height: 50%;
                        left: 32%;
                        position: absolute;
                        transform: rotate(60deg);
                        width: 50%;
                        z-index: 0;
                    }
            
                    img {
                        position: relative;
                        max-width: 380px;
                        width: 100%;
                        z-index: 1;
                    }
                }
            }
        
            button {
                background-color: #F8B045;
                border: none;
                border-radius: .5rem;
                box-shadow: 0px 3px 5px #999;
                font-size: 1.1rem;
                font-weight: bold;
                margin: 0 auto;
                margin-top: 1.5rem;
                max-width: 400px;
                padding: .8rem;
                transition: .3s;
                width: 100%;
        
                &:active, &:hover {
                    background-color: #e4a443;
                    cursor: pointer;
                }
            }
        }

        @keyframes entryAnimation {
            from {
                margin-bottom: -2rem;
                opacity: 0;
            } to {
                margin-bottom: 0;
                opacity: 1;
            }
        }
    }


    // Quem somos
    .quem-somos {
        background-color: #e5e5e5;
        width: 100%;
    
        .container-quem-somos {
            display: flex;
            flex-direction: column;
            gap: 2rem;
            margin: 0 auto;
            max-width: 1400px;
            padding-block: 2rem 3rem;
            padding-inline: 1.5rem;
    
            .parte-texto {
                align-items: center;
                display: flex;
                flex-direction: column;
                gap: 2rem;
        
                p {
                    line-height: 1.5rem;
                    max-width: 760px;
                    position: relative;
                    z-index: 1;
        
                    &::before {
                        content: '';
                        background-color: rgba(180, 36, 36, .1);
                        border-radius: 2rem;
                        height: 300px;
                        left: -15rem;
                        position: absolute;
                        top: -1rem;
                        transform: rotate(40deg);
                        width: 300px;
                        z-index: -1;
                    }
                }
            }
            
            picture {
                border-radius: .5rem;
                overflow: hidden;
        
                img {
                    box-shadow: 0 0 3px 1px rgba(0, 0, 0, .2);
                    border-radius: .5rem;
                    margin: 0 auto;
                    max-width: 400px;
                    transition: 1s;
                }
            }
        }
    }

    // Nossos serviços
    .nossos-servicos {
        background-color: #f1f1f1;
        width: 100%;

        .container-nossos-servicos {
            display: flex;
            flex-direction: column;
            gap: 2rem;
            overflow: hidden;
            padding-block: 2rem;
            padding-inline: 1.5rem;
        
            .titulo-servicos {
                position: relative;
        
                &::after {
                    background-color: #b42424;
                    bottom: -.25rem;
                    content: '';
                    height: 1.5px;
                    left: 2px;
                    position: absolute;
                    width: 25px;
                }
            }
        
            .parte-texto {
                align-items: center;
                display: flex;
                flex-direction: column;
                gap: 2rem;
            }
        
            .container-servicos {
                display: flex;
                flex-direction: column;
                gap: 2.5rem;
        
                .servicos {
                    display: flex;
                    flex-direction: column;
                    gap: 1rem;
                    margin: 0 auto;
                    max-width: 500px;
                    
                    picture {
                        height: 100%;
                        position: relative;
                        width: 100%;
        
                        img {
                            background-color: #f9f9f9;
                            border-radius: .5rem;
                            box-shadow: 1px 0px 3px 1px rgba(0, 0, 0, .1);
                            margin: 0 auto;
                            height: 550px;
                            object-fit: cover;
                            width: 400px;
                        }
        
                        img.clique {
                            animation: opacityClique 12s ease-in-out infinite;
                            bottom: 10px;
                            left: 10px;
                            position: absolute;
                            height: 20px;
                            opacity: 0;
                            width: 20px;
                            z-index: 2;
        
                            @keyframes opacityClique {
                                0%, 20%, 80%, 100% {
                                    opacity: 0;
                                } 50% {
                                    opacity: 1;
                                }
                            }
                        }
                    }
                }
        
                .demais-servicos {
                    align-items: center;
                    display: flex;
                    flex-direction: column;
                    gap: 1rem;
                    position: relative;
        
                    &::before {
                        content: '';
                        background-color: rgba(180, 36, 36, .1);
                        border-radius: 2rem;
                        height: 250px;
                        right: -200px;
                        position: absolute;
                        top: 4rem;
                        transform: rotate(40deg);
                        width: 300px;
                        z-index: -1;
                    }
        
                    h3 {
                        color: #b42424;
                        font-weight: 500;
                    }
        
                    ul {
                        list-style-type: square;
                        width: 350px;
        
                        li {
                            padding-block: .18rem;
                        }
                    }
                }
            }
        }
    }

    //Avaliações
    .avaliacoes {
        background-color: #efefef;
        width: 100%;

        .container-avaliacoes {
            align-items: center;
            display: flex;
            flex-direction: column;
            gap: 3rem;
            padding: 3rem 1.5rem;
    
            .titulo-section {
        
                &::after {
                    background-color: #F8B045;
                }
            }
        
            .cards {
                align-items: center;
                display: flex;
                flex-direction: column;
                gap: 2.5rem;
                position: relative;
        
                .line {
                    background-color: #F8B045;
                    height: 100%;
                    position: absolute;
                    width: 3px;
                    z-index: 0;
                }
        
                .card {
                    align-items: center;
                    background-color: #fff;
                    border-radius: 1.2rem;
                    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
                    display: flex;
                    flex-direction: column;
                    gap: 1.5rem;
                    max-width: 450px;
                    padding: 2rem 0;
                    position: relative;
                    text-align: center;
                    width: 350px;
                    z-index: 1;

                    .infos {
                        color: #555;
                        line-height: 1.5rem;
                        max-width: 65%;
                    }
        
                    .mensagem {
                        font-size: 1.1rem;
                        font-weight: 500;
                        max-width: 80%;
        
                        span {
                            color: #b42424;
                        }
                    }
                }
            }
        }
    }

    // Areas de atuação
    .localidades {
        width: 100%;

        .container-localidades {
            align-items: center;
            display: flex;
            flex-direction: column;
            gap: 3rem;
            padding-block: 2rem;
            padding-inline: 1.5rem;
            width: 100%;

            h1 {
    
                &::after {
                    background-color: #3150A1;
                }
            }
            
            .conteudo {
                align-items: center;
                display: flex;
                flex-direction: column;
                gap: 3rem;
    
                picture {
                    img {
                        width: 100%;
                        max-width: 380px;
                    }
                }
            
                .lista-localidades {
                    align-items: center;
                    display: flex;
                    flex-direction: column;
                    gap: 1rem;
            
                    h3 {
                        font-weight: 500;
            
                        span {
                            color: #3150A1;
                            font-weight: bold;
                        }
                    }
            
                    ul {
                        text-align: center;
            
                        li {
                            padding-block: .2rem;
            
                            &:last-child {
                                color: #3150A1;
                                font-weight: 500;
                                line-height: 1.3rem;
                                padding-top: 1rem;
                            }
                        }
                    }
                }

            }
        }
    }

    //Contato
    .contato {
        background-color: rgb(81, 63, 63);
        width: 100%;

        .container-contato {
            align-items: center;
            color: #fff;
            display: flex;
            flex-direction: column;
            gap: 2rem;
            padding-block: 4rem;
            text-align: center;

            .part1,
            .part2 {
                align-items: center;
                display: flex;
                flex-direction: column;
                gap: 1rem;
            }
    
            .part1 {
                max-width: 350px;
                .text {
                    font-size: 1.1rem;
                    padding-block: 1rem;
                }
            
                .redes-sociais {
                    display: flex;
                    gap: .6rem;
            
                    a {
                        position: relative;
            
                        img {
                            object-fit: contain;
                        }

                        &:nth-child(4) {

                            p {
                                color: #aaa;
                                font-size: .8rem;
                                position: absolute;
                                width: 90px;
                            }
                        }
                    }
                }
            }

            picture {
            
                img {
                    margin: 0 auto;
                    margin-bottom: 1.5rem;
                    max-width: 110px;
                    width: 100%;
                }
            }

            .part2 {
            
                .desenvolvedor {
                    display: flex;
                    flex-direction: column;
                    gap: .2rem;
                    margin-block: 2rem;
            
                    p {
                        font-weight: lighter;
                    }
            
                    a {
                        i {
                            font-size: 1.5rem;
                            padding: .2rem;
                            transition: .1s;
    
                            &:active, &:hover {
                                color: rgb(0, 119, 183);
                                cursor: pointer;
                            }
                        }
                    }
                }
            }
        }
    }

    .btnScrollTop {
        display: none;
    }

    [animation1="bottom"] {
        animation: animationEntryB 1.5s ease-in both;
    }
    @keyframes animationEntryB {
        from {
            margin-top: 3rem;
            opacity: 0;
        }
        to {
            margin-top: 0;
            opacity: 1;
        }
    }

    [animation2="bottom"] {
        animation: animationEntryB 1.5s ease-in both;
    }
    [animation2="left"] {
        animation: animationEntryL 1.5s ease-in both;
    }
    @keyframes animationEntryL {
        from {
            margin-left: -3rem;
            opacity: 0;
        }
        to {
            margin-left: 0;
            opacity: 1;
        }
    }

}



///////////////////////////////////////////////////////////// DESKTOP

@media (min-width: 1024px) {
    main {
        header {
            background: none;
            position: static;
            width: 100%;
    
            .container {
                justify-content: space-around;
    
                .logo {
                    img {
                        width: 65px;
                    }
                }
    
                svg {
                    display: none;
                }
    
                nav {
                    align-items: flex-end;
                    background: none;
                    overflow: visible;
                    position: static;
                    width: auto;
    
                    ul {
                        flex-direction: row;
                        gap: 1.5rem;
                        height: auto;
                        position: static;
                        text-align: none;
                        width: auto;
    
                        li {
                            margin: 0;
                            padding: 0;
                            width: auto;
    
                            a {
                                color: #000;
                                cursor: pointer;
                                font-size: clamp(.9rem, 1vw, 1rem);
                                font-weight: normal;
                                position: relative;
    
                                &::after {
                                    background-color: #b42424;
                                    bottom: -3px;
                                    content: '';
                                    height: 2px;
                                    left: 1px;
                                    position: absolute;
                                    transition: .3s;
                                    width: 0;
                                }
    
                                &:hover {
    
                                    &::after {
                                    width: 26px;
                                }
                                }
                            }
                        }
                    }
    
                    .redes-sociais {
                        display: none;
                    }
                }
            }
        }


        //Home
        .home {
            background-image: linear-gradient(to bottom, #f1f1f1 56.5%, #e5e5e5 43.5%);
            height: 90vh;

            .container-conteudo {
                align-items: center;
                justify-content: center;
                margin: 0 auto;
                padding: 0;
                max-width: 1150px;
    
                button {
                    display: none;
                }
                
                .conteudo {
                    align-items: center;
                    flex-direction: row;
                    justify-content: space-around;
    
                    .text {
                        gap: 1.5rem;
    
                        h1,
                        p {
                            width: 100%;
                        }
                        
                        .btn-home {
                            display: block;
                            margin: 0;
                            margin-block-start: 1rem;
                        }
                    }
    
                    picture {
                        
                        &::before {
                            bottom: 20%;
                            border-radius: 100%;
                            box-shadow: 0 0 60px 20px #000;
                            content: '';
                            height: 50%;
                            left: 20%;
                            padding-inline: 5rem;
                            position: absolute;
                            transform: rotate(50deg);
                            width: 55%;
                            z-index: 0;
                        }
                
                        img {
                            transition: .4s;
                            max-width: 100%;
    
                            &:hover {
                                transform: scale(1.05);
                            }
                        }
                    }
                }
            }
        }


        //Quem somos
        .quem-somos {
            height: 75vh;
            
            .container-quem-somos {
                align-items: center;
                flex-direction: row;
                gap: 0;
                height: 80%;
                padding: 0;
                max-width: 1150px;
    
                .parte-texto {
                    align-items: flex-start;
                    padding-left: 2rem;
                    width: 90%;

                    p {
                        &::before {
                            background-color: rgba(180, 36, 36, .2);
                            height: 300px;
                            left: -110%;
                            width: 300px;
                        }
                    }
                }
            }
    
            picture {
                justify-content: flex-end;
                width: 100%;
    
                img {
                    border-radius: .5rem;
                    max-height: 400px;
                }
            }
        }

        .nossos-servicos {
            .container-nossos-servicos {
                gap: 4rem;
                margin: 0 auto;
                max-width: 1150px;
                padding-block: 4rem;

                .parte-texto {
                    align-items: flex-start;
                }

                .container-servicos {
                    flex-direction: row;
                    flex-wrap: wrap;
                    gap: 2rem 0;
                    justify-content: space-around;
                    width: 100%;

                    .servicos {
                        margin: 0;
                        padding-block: 1rem;

                        picture {
                            transition: .3s;
                            
                            img {
                                max-height: 440px;
                            }

                            &:hover {
                                cursor: pointer;
                                transform: scale(1.01);
                            }
                        }
                    }

                    .demais-servicos {
                        align-items: flex-start;
                        gap: 2rem;
                        margin-top: 1rem;
                        padding-left: 5rem;
                        width: 100%;

                        &::before {
                            right: -8rem;
                        }

                        ul {
                            display: flex;
                            gap: 3rem;
                            width: auto;
                        }
                    }
                }
            }
        }

        .avaliacoes {
            height: 60vh;

            .container-avaliacoes {
                height: 100%;
                justify-content: center;
                margin: 0 auto;
                max-width: 1150px;

                .titulo-section {
                    align-self: flex-start;
                }
    
                .cards {
                    flex-direction: row;
                    justify-content: center;
                    max-width: 100%;
    
                    .line {
                        background-color: #F8B045;
                        bottom: 50%;
                        height: 4px;
                        position: absolute;
                        width: 60%;
                        z-index: 0;
                    }
                }
            }
        }

        .localidades {

            .container-localidades {
                align-items: flex-start;
                margin: 0 auto;
                max-width: 1150px;
                padding-block: 4rem;

                .conteudo {
                    flex-direction: row;
                    justify-content: space-between;
                    width: 100%;

                    picture {
                        img {
                            max-width: 500px;
                        }
                    }

                    .lista-localidades {

                        h3 {
                            font-size: 1.2rem;
                        }
                        ul {
                            li {
                                font-size: 1.2rem;
                            }
                        }
                    }
                }
            }
        }

        .contato {

            .container-contato {
                flex-direction: row;
                gap: 3rem;
                justify-content: center;
                margin: 0 auto;
                max-width: 1150px;

                picture {
                    width: 400px;
                
                    img {
                        margin: 0 auto;
                        width: 100px;
                    }

                    p {
                        padding: 1rem;
                    }
                }

                .part1 {
                    align-items: flex-start;
                    text-align: left;

                    .redes-sociais {
                        a {
                            position: relative;
                            width: 40px;
                            z-index: 1;

                            img:hover {
                                cursor: pointer;
                                transform: scale(1.05);
                                transition: .3s;
                            }

                            &:hover::before {
                                content: '';
                                box-shadow: 0 0 20px 2px #F8B045;
                                border-radius: 1rem;
                                bottom: .5rem;
                                height: 20px;
                                left: .7rem;
                                position: absolute;
                                transition: 1s;
                                z-index: -1;
                            }
                        }
                    }
                }

                .part2 {
                    .desenvolvedor {
                        margin: 0;
                        margin-top: .5rem;
                    }
                }
            }
        }

        .btnScrollTop {
            animation: animateBtn 3s ease-in-out infinite;
            background: none;
            border: none;
            bottom: 7%;
            display: block;
            right: 7%;
            cursor: pointer;
            position: fixed;
            width: 40px;
            z-index: 2;
        
            svg {
                stroke: rgb(119, 33, 33);

                &:hover, &:active {
                    fill: rgba(228, 164, 67, 0.9);
                }

            }

            @keyframes animateBtn {
                0%, 100% {
                    margin-bottom: 0;
                } 50% {
                    margin-bottom: -.5rem;
                }
            }
        }

    } //main
}// mediaq